@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  width: 100%;
  background-color: #ecf7f7;
  overflow-x: hidden;
}

.card {
  width: 250px;
  height: 250px;
  border: 1px solid #beb9b9;
  border-radius: 4px;
  position: relative;
  margin: 30px;
  transition: all 0.5s;
  background-color: #fff;
  cursor: pointer;
}
.card_img_container {
  width: 100%;
  height: 60%;
}
.card_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px 4px 0px 0px;
}
.card_circle_icon {
  position: absolute;
  top: 53%;
  right: 3%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #beb9b9;
  background-color: #fff;
  color: #000;
  font-size: 15px;
  font-family: "Poppins", sans-serif;
}
.userIcon {
  width: 100%;
  color: #3a3939;
}
.card_description {
  width: 100%;
  height: 40%;
  padding: 10px 5px;
  font-family: "Poppins", sans-serif;
}
.card_title {
  width: 100%;
  color: #000;
  font-weight: bold;
  font-size: 1vw;
  display: flex;
  justify-content: center;
}
.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
@media screen and (max-width: 768px) {
  .card {
    margin: 20px;
  }
  .card_title {
    font-size: 2vw;
  }
}

@media screen and (max-width: 480px) {
  .card {
    margin: 20px 0px;
  }
  .card_title {
    font-size: 4vw;
  }
}

.homescreen_container {
  width: 100%;
  background-color: #ecf7f7;
}
.page_heading {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  text-transform: capitalize;
  font-family: "Poppins";
  font-size: 2vw;
  background-color: #73458a;
  color: #fff;
  padding: 10px;
  position: fixed;
  top: 0;
  z-index: 3;
}
.card_container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 60px 40px 20px;
}
.extra_card {
  display: none;
}
@media screen and (min-width: 1300px) {
  .card_container {
    justify-content: flex-start;
    margin-left: 20px;
  }
  .card {
    margin: 40px;
  }
}
@media screen and (min-width: 992px) {
  .extra_card {
    width: 250px;
    display: block;
    margin: 30px;
  }
}
@media screen and (max-width: 768px) {
  .page_heading {
    font-size: 2.3vw;
  }
  .card_container {
    justify-content: space-around;
    padding: 60px 20px 20px;
  }
  .extra_card {
    display: none;
  }
}
@media screen and (max-width: 480px) {
  .page_heading {
    font-size: 7vw;
  }
  .extra_card {
    display: none;
  }
}

